import React from 'react';
import Swipe from '../common/Swipe'
import './Detail.css'
import List from '../common/List';
import {NavLink} from 'react-router-dom'
import * as types from '../store/type'
import { connect } from 'react-redux';
import { asyncActionPro, asyncActionDetail,asyncActionShopCar } from '../store/anyscAction';
import querystring from 'query-string'
import QueueAnim from 'rc-queue-anim';

class Detail extends React.Component{
    constructor(){
        super()
        this.state={
            num:1
        }
    }
    componentWillReceiveProps(nextProps){
        if(this.props.user !== nextProps.user){
            this.props.changeUser(nextProps.user)
        }
    }
    componentDidMount(){
        let goodId = this.props.match.params.goodId;
        let dataName = querystring.parse(this.props.location.search).dataName;
        this.props.getList({goodName:dataName},types.UPDATE_LIST)
        this.props.getDetail({goodName:dataName},goodId)
    }
    render(){
    return <QueueAnim animConfig={{opacity:[1, 0]}} duration="3000">
    <div className="detail" key={1}>
    <div style={{maxHeight:'40.5rem',overflow:'auto'}}>
        <Swipe data={{src1:this.props.detail.src,src2:this.props.detail.src,src3:this.props.detail.src}}></Swipe>
        <div className="p_des_bg">
    <div className="p_des">
        <table style={{width:'93%',border:0, margin:'10px auto'}} cellSpacing="0" cellPadding="0">
          <tbody><tr>
            <td colSpan="2" className="p_name">RIO锐澳鸡尾酒Hello Kitty限量预调伏特加果洋酒275ml*6瓶 </td>
          </tr>
          <tr>
            <td colSpan="2" className="p_spe">“开业巨惠，北京专柜直供”，不光低价，“真”才靠谱！</td>
          </tr>
          <tr>
            <td className="p_con" style={{width:'6.5rem'}}>本店价格：</td>
            <td className="p_con"><font>￥{this.props.detail.price}</font></td>
          </tr>
          <tr>
            <td className="p_con">品牌：</td>
            <td className="p_con">{this.props.detail.name}</td>
          </tr>
        </tbody></table>
    </div>
    <div className="p_des">
        <table style={{width:'93%',border:0, margin:'10px auto'}} cellSpacing="0" cellPadding="0">
          <tbody><tr>
            <td className="p_con" style={{width:'4.9rem'}}>已选</td>
            <td className="p_con">{this.state.num}件</td>
          </tr>
          <tr>
            <td className="p_con">数量</td>
            <td className="p_con">
                <div className="j_nums">
                    <input type="button" defaultValue="" onClick={this.reduce.bind(this)}  className="n_btn_2"/> 
                    <span type="text" style={{textAlign:"center",lineHeight:
                '3.5rem'}} className="n_ipt"> {this.state.num} </span>
                    <input type="button" defaultValue="" onClick={this.add.bind(this)} className="n_btn_1"/>
                </div>
            </td>
          </tr>
        </tbody></table>
    </div>
    
    <div className="des_border">
        <div className="des_tit">
            <ul>
                <li className="current"><a>商品属性</a></li>
                <li><a>商品详情</a></li>
                <li><a>商品评论</a></li>
            </ul>
        </div>
        <div className="des_con" id="p_attribute" name="p_attribute">
            
            <table className="d_tab" style={{width:'93%', margin:'0 auto',align:'center',border:0}} cellSpacing="0" cellPadding="0">
              <tbody><tr>
                <td>商品名称：迪奥香水</td>
                <td>商品编号：1546211</td>
              </tr>
              <tr>
                <td>商品毛重：160.00g</td>
                <td>商品产地：法国</td>
              </tr>
              <tr>
                <td>容量：1ml-15ml </td>
                <td>类型：女士香水，Q版香水，组合套装</td>
              </tr>
            </tbody></table>
                
        </div>
    </div> 
    
    <div className="des_border" id="p_details" name="p_details">
        <div className="des_t">商品详情</div>
        <div className="des_con">
            
            <p align="center">
            <img src={this.props.detail.src} style={{width:'100%'}}/>
            </p>
            
        </div>
    </div> 
    
    <div className="des_border" id="p_comment" name="p_comment">
        <div className="des_t">商品评论</div>
        
        <table className="jud_tab" style={{width:'93%', margin:'3% auto 0 auto',align:'center',border:0}} cellSpacing="0" cellPadding="0">
          <tbody><tr>
            <td style={{width:'25%'}} className="jud_per">
                <p>80.0%</p>好评度
            </td>
            <td style={{width:'75%'}}>
                <table className="j_tab"cellSpacing="0" cellPadding="0" style={{width:'93%', fontSize:'1.1em',border:0}}>
                  <tbody><tr>
                    <td width="90">好评<font>（80%）</font></td>
                    <td><img src="/image/pl.gif"/></td>
                  </tr>
                  <tr>
                    <td>中评<font>（20%）</font></td>
                    <td><img src="/image/pl.gif"/></td>
                  </tr>
                  <tr>
                    <td>差评<font>（0%）</font></td>
                    <td><img src="/image/pl.gif"/></td>
                  </tr>
                </tbody></table>
            </td>
          </tr>
        </tbody></table>
                        
        <table className="jud_list" style={{width:'93%', marginTop:'30px',border:0}}cellSpacing="0" cellPadding="0">
          <tbody>
              <tr style={{valign:'top'}}>
            <td style={{width:'28%'}}><img src="/image/s_head.jpg" style={{width:'2.7rem',height:'2.7rem'}}/>&nbsp;向死而生</td>
            <td style={{width:'28%'}}>
                颜色分类：<font>粉色</font> <br/>
                型号：<font>50ml</font>
            </td>
            <td style={{textAlign:'right'}}>
                产品很好，香味很喜欢，必须给赞。 <br/>
                <font>2015-09-24</font>
            </td>
          </tr>
          <tr style={{valign:'top'}}>
            <td style={{width:'28%'}}><img src="/image/s_head.jpg" style={{width:'2.7rem',height:'2.7rem'}}/>&nbsp;就是这么想的</td>
            <td style={{width:'28%'}}>
                颜色分类：<font>粉色</font> <br/>
                型号：<font>50ml</font>
            </td>
            <td style={{textAlign:'right'}}>
                送朋友，她很喜欢，大爱。 <br/>
                <font>2015-09-24</font>
            </td>
          </tr>
          <tr style={{valign:'top'}}>
            <td style={{width:'28%'}}><img src="/image/s_head.jpg" style={{width:'2.7rem',height:'2.7rem'}}/>&nbsp;墨镜墨镜</td>
            <td style={{width:'28%'}}>
                颜色分类：<font>粉色</font> <br/>
                型号：<font>50ml</font>
            </td>
            <td style={{textAlign:'right'}}>
                大家都说不错<br/>
                <font>2015-09-24</font>
            </td>
          </tr>
          <tr style={{valign:'top'}}>
            <td style={{width:'28%'}}><img src="/image/s_head.jpg" style={{width:'2.7rem',height:'2.7rem'}}/>&nbsp;那*****洋 <br/><font>（匿名用户）</font></td>
            <td style={{width:'28%'}}>
                颜色分类：<font>粉色</font> <br/>
                型号：<font>50ml</font>
            </td>
            <td style={{textAlign:'right'}}>
                下次还会来买，推荐。<br/>
                <font>2015-09-24</font>
            </td>
          </tr>
        </tbody></table>        
    </div>
    <table className="b_tab" style={{width:'93%', margin:'4% auto 0 auto',border:0}} cellSpacing="0" cellPadding="0">
      <tbody><tr>
        <td width="43%"><img src="/image/b_img.jpg" style={{width:'23rem'}}/></td>
        <td width="57%"><div className="brand_btn"><a>进入品牌专区</a></div></td>
      </tr>
    </tbody></table>
	
    <div className="i_t">
        <span className="fl f_c">同类产品推荐</span>
    </div>
    <List data={{list:this.props.list}}></List>
    
    </div>
</div>            

{/* <!--Begin Footer Begin--> */}
<div className="p_btm">
	<ul>
    	<li className="b_1"><a><p></p>关注</a></li>
        <li className="b_2"><a><p></p>购物车</a></li>
        <li className="b_3"><a onClick={this.show.bind(this)}>加入购物车</a></li>
        <li className="b_4"><a onClick={this.close.bind(this)}>立即购买</a></li>
    </ul>
</div>
{/* <!--End Footer End--> */}

{/* <!--Begin 弹出层-加入购物车 Begin--> */}
    {/* <QueueAnim animConfig={{opacity:[1, 0]}} duration="3000"> */}
    <div key={2} id="fade" className="black_overlay"></div>
    <div key={3} id="MyDiv" className="white_content">             
        <div className="white_d">
            <div className="notice_t">
                商品加入购物车
                <span  className="fr" style={{position:'absolute', top:'1.3rem', right:'.8rem', cursor:'pointer'}}><img src="/image/close.gif"/></span>
            </div>
            <div className="notice_c">
                <table border="0" style={{width:'100%', margin:'2% auto'}} cellSpacing="0" cellPadding="0">
                <tbody><tr style={{valign:'top'}}>
                    <td rowSpan="2" style={{paddingBottom:'1.3rem',width:'11.6rem'}}><div className="c_img fl"><img src={this.props.detail.src} style={{width:'9.8rem',height:'9.8rem'}}/></div></td>
                    <td>{this.props.detail.name} 275ml</td>
                </tr>
                <tr style={{valign:'top'}}>
                    <td style={{paddingBottom:'1.3rem'}}>
                        数量：{this.state.num}件 <br/>
                        本店价格：<font>￥{this.props.detail.price}</font> <br/>
                        消费积分： 28R <br/>
                    </td>
                </tr>
                <tr>
                    <td colSpan="2" style={{borderTop:'1px solid #e2e2e2', textAlign:'center', paddingTop:'1.3rem'}}>
                        <NavLink to="/product" className="b_sure">继续购物</NavLink> <a onClick={this.close.bind(this)} className="b_sure">确定</a>
                    </td>
                </tr>
                </tbody></table>
            </div>
        </div>
    </div>
    {/* </QueueAnim> */}
    </div>
    </QueueAnim>
   }
   show(){
        this.oFade = document.getElementById("fade");
        this.oMyDiv = document.getElementById("MyDiv");
        this.oFade.style.display = "block";
        this.oMyDiv.style.display = "block";
   }
   close(){
       if(this.props.user!=undefined){
        this.oFade = document.getElementById("fade");
        this.oMyDiv = document.getElementById("MyDiv");
        let user = {...this.props.user};
        this.props.detail.number = this.state.num;
        user.shopCar = this.props.detail;
        this.props.changeShopCar(user.shopCar)
        this.oFade.style.display = "none";
        this.oMyDiv.style.display = "none";
        this.props.history.push("/shopcar")
       }else{
           this.props.history.push("/login")
       }
   }
   add(){
       this.setState({num:++this.state.num})
   }
   reduce(){
       this.setState({num:--this.state.num>0?this.state.num:1})
   }
}

let MapStateToProps = (state,props)=>({
    list:state.list,
    detail:{...state.detail,number:0},
    user:state.user
})
   

let MapDispatchToProps = (dispatch)=>({
    getList:(opt,type)=>{asyncActionPro(
        opt,
        dispatch,
        type
    )},
    getDetail:(opt,index)=>{asyncActionDetail(
        opt,
        dispatch,
        types.UPDATE_DETAIL,
        index
    )},
    changeShopCar:(data)=>{dispatch({type:types.UPDATE_SHOPCAR,payload:data})},
    changeUser:(opt)=>{asyncActionShopCar(
        opt,
        dispatch,
        types.UPDATE_USER
    )}
})

export default connect(
    MapStateToProps,
    MapDispatchToProps
)(Detail)