import React from 'react'
import './List.css'
import {NavLink} from 'react-router-dom'
import querystring from 'query-string'

let List = (props) => {
    let {list} = props.data
    return <div className="list">
                <div className="like">
                    <ul>
                        {
                            list.map((item,index)=>(
                                <li key={item.goodId}>
                                    <div className="img"><NavLink to={{
                                        pathname:'/detail/'+item.goodId,
                                        search:querystring.stringify({
                                            dataName:"list"
                                        })
                                    }}><img src={item.src} style={{width:'15.rem'}}/></NavLink></div>                                        
                                    <div className="name">
                                        <NavLink to={{
                                        pathname:'/detail/'+item.goodId,
                                        search:querystring.stringify({
                                            dataName:"list"
                                        })
                                    }}><h2>{item.name}</h2>{item.detail}</NavLink>
                                    </div>
                                    <div className="price">
                                        <font>￥<span>{item.price}</span></font> &nbsp; {item.size}
                                    </div>
                                </li>
                            ))
                        }
                    </ul>
                </div>
            </div>
}

export default List